@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;500;900&display=swap);
/* font family */
/* Variable Setting */
:root {
  --main-color: #ebe2de;
  --index-bg-color: #214869;
  --main-gray: #f0f0f0;
  --font-gray: #999999;
}

/* Basic CSS setting */
* {
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  outline: none;
  font-family: "Noto Sans KR", sans-serif;
}

* button:focus {
  outline: none;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f7f3f3;
}

.wrap {
  margin-top: 120px;
  width: 1128px;
  height: 643px;
  box-shadow: 5px 45px 30px rgba(21, 32, 95, 1);
}

.main {
  overflow-y: auto;
}

.main::-webkit-scrollbar {
  /* 스크롤바 전체 */
  width: 5px;
  height: 15px;
}

.main::-webkit-scrollbar-thumb {
  /* 스크롤바 막대 */
  background: #ebe2de;
  background: var(--main-color);
  border-radius: 15px;
}
.main::-webkit-scrollbar-track {
  /* 스크롤바 막대외부 */
  background-color: transparent;
}

/* 태블릿 */
@media screen and (max-width: 1024px) {
  .wrap {
    width: 750px;
  }
}

.inner h2 {
  color: #ebe2de;
}
h3 {
  color: #508164;
}

.inner .introheader {
  height: 150px;
  padding: 30px;
  background-color: #508164;
}

.inner .introheader h2 {
  font-size: 30px;
}

.inner .introheader p {
  position: relative;
  color: white;
  display: inline-block;
  height: 21px;
  overflow: hidden;
  animation: typing 8s steps(40, end) infinite;
}

.inner .introheader p::after {
  position: absolute;
  display: block;
  content: "";
  animation: cursor 0.5s step-end infinite;
}

.inner .introskills h3,
.inner .introprojects h3,
.inner .introblog h3 {
  font-size: 18px;
}

.inner .introskills p,
.inner .introprojects p,
.inner .introblog p {
  font-size: 14px;
}

.inner .introskills p,
.inner .introblog p {
  color: var(--font-gray);
}

/* ------------------ 스킬셋 ------------------*/
.inner .introskills {
  height: 350px;
  padding: 30px;
}

.inner .introskills .skillset .skills {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 226px;
  height: 150px;
  border-radius: 15px;
  box-shadow: 2px 2px 10px rgb(167, 167, 167, 0.5);
  position: relative;
}

.skillset .skills img {
  position: absolute;
  top: -38px;
  left: 72px;
  width: 80px;
  height: 80px;
}

.inner .introprojects {
  height: 430px;
  background-color: var(--main-gray);
  padding: 30px;
}

.inner .introprojects .projectset .projectbox {
  width: 450px;
  height: 240px;
  box-shadow: 2px 2px 10px rgb(167, 167, 167, 0.5);
  background-color: #fff;
  position: relative;
}

.projectset .projectbox .pro_caption {
  position: absolute;
  color: #fff;
  top: 0px;
  width: 450px;
  height: 240px;
  background-color: #8e6d6e;
  opacity: 0;
  transition: all 0.2s ease-in-out;
  z-index: 10;
}

.projectset .projectbox .pro_caption p {
  font-size: 14px;
  text-align: right;
  margin-bottom: 10px;
}

.projectset .projectbox .pro_caption p:first-child {
  font-weight: bold;
  font-size: 20px;
}

.projectset .projectbox .pro_caption button {
  font-weight: bold;
}

.projectset .projectbox .pro_caption button:hover {
  color: var(--main-color);
}

.projectset .projectbox:hover .pro_caption {
  opacity: 1;
  width: 225px;
  transform: translateX(113px);
}

@media screen and (max-width: 1024px) {
  .inner .introprojects .projectset .projectbox {
    margin-bottom: 20px;
  }

  .inner .introprojects {
    height: 700px;
  }

  .inner .introblog {
    height: 490px;
  }

  .inner .introblog .blogbox {
    width: 540px;
  }

  .inner .introblog .blogbox .blogentry {
    width: 250px;
  }
}

@keyframes typing {
  0% {
    width: 0%;
  }
  50% {
    width: 100%;
  }
  100% {
    width: 0%;
  }
}

.text_emphasize {
  color: #44564a;
}

.menu .githubbox {
  width: 120px;
  height: 30px;
  border-radius: 5px;
  background-color: #fff;
}

.menu .githubbox:hover {
  background-color: var(--main-color);
  color: white;
}

.menu .githubbox .githubPtag {
  width: 95px;
}

.menu ul li {
  margin-top: 10px;
  font-size: 14px;
}

.menu ul a {
  width: 200px;
  padding: 0 10px 10px 20px;
  margin-right: 1px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: var(--main-gray);
}

.menu ul a.active {
  background-color: white;
  font-weight: bold;
  color: var(--main-color);
  position: relative;
}

.menu ul a .spanActive {
  position: absolute;
  top: 16px;
  right: 20px;
  width: 10px;
  height: 10px;
  border-radius: 10px;
  /* background-color: #4e8fc6; */
  display: none;
}

.menu ul a .spanActive.show {
  background-color: #4e8fc6;
  display: block;
}

/* 태블릿 */
@media screen and (max-width: 1024px) {
  .menu ul a {
    width: 130px;
  }

  .menu ul a .spanActive.show {
    display: none;
  }
}

.introinner section {
  font-weight: 300;
  padding: 35px 40px;
}

.introinner a:hover {
  color: var(--main-color);
}

.introinner .introheader {
  padding-top: 0px;
}

.introinner .resumeArea {
  position: -webkit-sticky;
  position: sticky;
  font-weight: 500;
  top: 10px;
  padding: 10px 40px 0 0;
}

.introinner h1 {
  font-size: 25px;
  margin-bottom: 10px;
}

.introinner h2 {
  font-weight: 500;
  color: var(--font-gray);
  margin-bottom: 20px;
}

.introinner p {
  text-align: justify;
  line-height: 1.5;
}

.introinner p {
  text-align: justify;
  line-height: 1.5;
}

.introinner .introstack .stackArea {
  margin-top: 30px;
}

.introinner .introstack .stackArea img {
  width: 80px;
  height: 80px;
}

.introinner .introstack .stackArea .stack {
  display: inline-flex;
  align-items: center;
  padding-left: 10px;
}

.introinner .introstack .stackArea .stack div:nth-child(1) {
  display: flex;
  width: 400px;
}

.introinner .introstack .stackArea .stack .caption {
  display: flex;
  align-items: center;
  flex: 1 1;
  padding-left: 20px;
}

.introinner .introstack .stackArea .stack ul li {
  display: inline-flex;
  align-items: center;
  width: 350px;
  height: 70px;
  margin-top: 10px;
}

.introinner .introstack .stackArea .stack ul li p {
  font-weight: 400;
}

.introinner .introstack .stackArea .stack ul li span {
  font-size: 12px;
}

.introinner .introstack .stackArea .stack ul li div:nth-child(1) {
  width: 40px;
  margin-right: 10px;
}

.introinner .introsubmenu ul li {
  padding: 10px 100px;
  border: 2px solid var(--main-gray);
}

.introinner .introsubmenu ul li:hover {
  color: var(--main-color);
  border: 2px solid var(--main-color);
}

.projectinner h1 {
  font-weight: 500;
  font-size: 25px;
  color: #508164;
}

.projectinner h2 {
  font-weight: 500;
  font-size: 20px;
}

.projectinner section {
  font-weight: 300;
  padding: 35px 40px;
}

.projectinner section:nth-child(1) {
  padding-bottom: 0px;
}

.projectinner .projectEntry ul li {
  width: 660px;
  height: 437px;
  background-color: tomato;
  margin: 20px 0 30px 0;
  border: 1px solid var(--main-gray);
  box-shadow: 2px 2px 10px rgb(167, 167, 167, 0.5);
  cursor: pointer;
}

.projectinner .projectEntry ul li img {
  width: 100%;
  height: 320px;
}

.projectinner .projectEntry ul li .pro_summary {
  width: 100%;
  height: 115px;
  padding: 20px;
  background-color: white;
}

